$(document).ready(function() {

    sliderMain();
    sliderAdvantages();
    sliderDirections();
    sliderProducts();
    sliderProduct();
    sliderAbout();
    sliderPartners();
    sliderMaterials();
    sliderCertificates();
    sliderProducts2();


    // Hover-Click
    $(".header__item").hover(onIn, onOut);
    $(".header__item .header__item-show").click(function() {
        if ($(this).parents(".header__item").hasClass("header__item--active")) {
            $(this).parents(".header__item").removeClass("header__item--active")
        } else {
            $(this).parents(".header__item").addClass("header__item--active")
        }
    })

    let ItemShow = $('.header__item-show')
    let Item = $('.header__item')

    $(document).mouseup(function (e) {
        if ( !ItemShow.is(e.target) && ItemShow.has(e.target).length === 0 &&
            !Item.is(e.target) && Item.has(e.target).length === 0

        ) {
            Item.removeClass('header__item--active')
        }
    })

    // Burger

    $('.header__burger').click(function () {
        $(this).toggleClass('header__burger--active')
        $('.header-bottom').toggleClass('header-bottom--active')
        $('body').toggleClass('fixed-body')
    })

    // Tabs
    $(".products__tab").click(function () {
        let path = $(this).attr("data-tab-path")
        $(this).siblings(".products__tab").removeClass("products__tab--active")
        $(this).addClass("products__tab--active")

        $(this).parents(".products__tabs").find(".products__slider").removeClass("products__slider--active")
        $(this).parents(".products__tabs").find(`.products__slider[data-content-path="${path}"]`).addClass("products__slider--active")
    })

    // Tabs2
    $(".products-info__tab").click(function () {
        let path = $(this).attr("data-tab-path")
        $(this).siblings(".products-info__tab").removeClass("products-info__tab--active")
        $(this).addClass("products-info__tab--active")

        $(this).parents(".products-info__tabs").find(".products-info__content").removeClass("products-info__content--active")
        $(this).parents(".products-info__tabs").find(`.products-info__content[data-content-path="${path}"]`).addClass("products-info__content--active")
    })

    // Up
    var btn = $('.up');
    $(window).scroll(function() {
        if ($(window).scrollTop() > 300) {
            btn.addClass('up--active');
        } else {
            btn.removeClass('up--active');
        }
    });
    btn.on('click', function(e) {
        e.preventDefault();
        $('html, body').animate({scrollTop:0}, '300');
    });

    // Accordion
    $(".answers__item-show").click((function(){
        $(this).toggleClass("answers__item-show--active"),
        $(this).siblings(".answers__item-hidden").slideToggle()
    }));

    // modal

    $("[data-modal]").click(function () {
        let modalId = $(this).attr("data-modal")

        openModal(modalId)
    })

    function openModal(modalId){
        $(`#${modalId}`).addClass("modal--active")
        $("body").addClass("fixed-body")
    }

    $(".modal").click(function () {
        closeModal()
    })

    $("._close-modal").click(function () {
        closeModal()
    })

    $(".modal__dialog").click(function (e) {
        e.stopPropagation()
    })


    function closeModal() {
        $('.modal').removeClass("modal--active")
        $("body").removeClass("fixed-body")
    }

})

function onIn() {
    if (window.innerWidth > 768) {
        let el = $(this)
        setTimeout(function() {
            if (el.is(':hover')) {
                console.log(el)
                el.addClass("header__item--active")

            }
        }, 200);
    }
}

function onOut() {
    if (window.innerWidth > 768) {
        $(this).removeClass("header__item--active")
    }
}